.style-transfer-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.image-upload-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.upload-section {
  width: 48%;
}

.file-input {
  margin-bottom: 10px;
}

.image-preview {
  width: 100%;
  height: 300px;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 10px;
}

.image-preview img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.transfer-button {
  background-color: #4285f4;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
}

.transfer-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.progress-container {
  margin: 20px 0;
  width: 100%;
  text-align: center;
}

.progress-bar {
  width: 100%;
  height: 20px;
  background-color: #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 10px;
}

.progress-fill {
  height: 100%;
  background-color: #4285f4;
  transition: width 0.3s ease;
}

/* Add or modify these styles to move the download button down */
.result-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.result-image {
  max-width: 100%;
  margin-bottom: 20px; /* Add space between image and button */
}

.download-button {
  margin-top: 20px; /* Add more space above the button */
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.download-button:hover {
  background-color: #45a049;
}

.download-button {
  display: inline-block;
  background-color: #34a853;
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 4px;
  margin-top: 10px;
}